import React from 'react';
import ScrollToTopButton from '../components/ScrollTopButton';

const DictsAndSets = () => {
  return (
    <div className="lesson-container">
      <div className="lesson-header">
        <h1>Словари и множества</h1>
      </div>

      <div className="lesson-section">
        <h2>Определение</h2>
        <p>
          Словари и множества - это основные структуры данных в Python:
          <ul>
            <li><strong>Словарь:</strong> Коллекция пар ключ-значение, где каждый ключ уникален и ассоциирован с определенным значением.</li>
            <li><strong>Множество:</strong> Неупорядоченная коллекция уникальных элементов.</li>
          </ul>
        </p>
      </div>

      <div className="lesson-section">
        <h2>Операции над словарями и множествами</h2>
        <p>
          Python предоставляет различные операции для работы со словарями и множествами:
          <ul>
            <li><strong>Доступ к элементам:</strong> Получение значений по ключам в словарях или перебор элементов в множествах.</li>
            <li><strong>Обновление:</strong> Изменение значений в словарях или добавление/удаление элементов в множествах.</li>
            <li><strong>Объединение:</strong> Слияние словарей или множеств для создания новых коллекций.</li>
            <li><strong>Сравнение:</strong> Проверка на равенство или отношения подмножества/надмножества.</li>
            <li><strong>Удаление:</strong> Удаление элементов по ключам из словарей или полностью удаление множеств.</li>
          </ul>
        </p>
      </div>

      <div className="lesson-section">
        <h2>Методы словарей и множеств</h2>

        <h3>Методы словарей</h3>
        <p> Примеры методов словарей:</p>
        <pre>
          <code>
            {`# Создание словаря\n`}
            {`student = {'name': 'John', 'age': 25, 'courses': ['Math', 'CompSci']}\n\n`}

            {`# Доступ к значениям\n`}
            {`print(student['name'])  # Вывод: John\n\n`}

            {`# Добавление новой пары ключ-значение\n`}
            {`student['phone'] = '555-1234'\n\n`}

            {`# Обновление значения\n`}
            {`student['age'] = 26\n\n`}

            {`# Удаление пары ключ-значение\n`}
            {`del student['courses']\n`}
          </code>
        </pre>

      <h3>Методы множеств</h3>
      <p>Примеры методов множеств:</p>
        <pre>
          <code>
            {`# Создание множества\n`}
            {`my_set = {1, 2, 3, 4, 5}\n\n`}

            {`# Добавление элементов\n`}
            {`my_set.add(6)\n\n`}

            {`# Удаление элементов\n`}
            {`my_set.remove(3)\n\n`}

            {`# Проверка наличия элемента\n`}
            {`print(2 in my_set)  # Вывод: True\n`}
          </code>
        </pre>
      </div>

=      <div className="lesson-footer">
        <p>Это завершает наш урок по словарям и множествам в Python. Практикуйтесь, используя эти 
          многофункциональные структуры данных!</p>
      </div>
      <ScrollToTopButton /> 
    </div>
  );
};

export default DictsAndSets;
